import {createStore} from 'vuex'
import {mutations} from './mutations'
 
export const store = createStore({
  state:{
    bannerPicList: [],
    bannerType: 1,
    lightList: [],
    lightIndex: 0,
    lightDiskList: [],
    lightDiskIndex: 0
  },
  getters:{
    getList(state){
      return state.bannerPicList
    }
  },
  mutations
})
