import { bannerPicConfig, bannerPicConfigEn } from '../assets/js/bannerPicConfig.js'

function chkWidth () {
  let w = document.body.clientWidth
  if (w > 992) {
    return '1'
  } else {
    return '2'
  }
}

export const mutations = {
  changeBannerType (state, type) {
    state.bannerType = type
  },
  changeBannerCn (state) {    
    state.bannerPicList = bannerPicConfig[state.bannerType + "-" + chkWidth()]
  },
  changeBannerEn (state) {
    state.bannerPicList = bannerPicConfigEn[state.bannerType + "-" + chkWidth()]
  },
  changeLightCn (state) {
    state.lightList = [
      {id: 'c40', current: true, bannerList: ['/images/light-c40-product1.png','/images/light-c40-product2.png','/images/light-c40-product3.png'], table1: '/images/cn/light-c40-table1.png', table2: '/images/cn/light-c40-table2.png'},
      {id: 'c50', current: false, bannerList: ['/images/light-c50-product3.png','/images/light-c50-product2.png','/images/light-c50-product1.png'], table1: '/images/cn/light-c50-table1.png', table2: '/images/cn/light-c50-table2.png'},
      {id: 'c60', current: false, bannerList: ['/images/light-c60-product2.png','/images/light-c60-product1.png','/images/light-c60-product3.png'], table1: '/images/cn/light-c60-table1.png', table2: '/images/cn/light-c60-table2.png'}
    ]
  },
  changeLightEn (state) {
    state.lightList = [
      {id: 'c40', current: true, bannerList: ['/images/light-c40-product1.png','/images/light-c40-product2.png','/images/light-c40-product3.png'], table1: '/images/cn/light-c40-table1.png', table2: '/images/cn/light-c40-table2.png'},
      {id: 'c50', current: false, bannerList: ['/images/light-c50-product3.png','/images/light-c50-product2.png','/images/light-c50-product1.png'], table1: '/images/cn/light-c50-table1.png', table2: '/images/cn/light-c50-table2.png'},
      {id: 'c60', current: false, bannerList: ['/images/light-c60-product2.png','/images/light-c60-product1.png','/images/light-c60-product3.png'], table1: '/images/cn/light-c60-table1.png', table2: '/images/cn/light-c60-table2.png'}
    ]
  },
  changeLightIndex (state, index) {
    state.lightIndex = index
    for (let i = 0; i < state.lightList.length; i++) {
      if (i === index) {
        state.lightList[i].current = true
      } else {
        state.lightList[i].current = false
      }
    }
  },
  changeLightDiskCn (state) {
    state.lightDiskList = [
      {id: 'c40', current: true, bannerList: ['/images/light-disk-c40-product1.png','/images/light-disk-c40-product2.png','/images/light-disk-c40-product3.png'], table1: '/images/cn/light-disk-c40-table1.png', table2: '/images/cn/light-disk-c40-table2.png'},
      {id: 'c50', current: false, bannerList: ['/images/light-disk-c50-product3.png','/images/light-disk-c50-product2.png','/images/light-disk-c50-product1.png'], table1: '/images/cn/light-disk-c50-table1.png', table2: '/images/cn/light-disk-c50-table2.png'},
      {id: 'c60', current: false, bannerList: ['/images/light-disk-c60-product2.png','/images/light-disk-c60-product1.png','/images/light-disk-c60-product3.png'], table1: '/images/cn/light-disk-c60-table1.png', table2: '/images/cn/light-disk-c60-table2.png'}
    ]
  },
  changeLightDiskEn (state) {
    state.lightDiskList = [
      {id: 'c40', current: true, bannerList: ['/images/light-disk-c40-product1.png','/images/light-disk-c40-product2.png','/images/light-disk-c40-product3.png'], table1: '/images/cn/light-disk-c40-table1.png', table2: '/images/cn/light-disk-c40-table2.png'},
      {id: 'c50', current: false, bannerList: ['/images/light-disk-c50-product3.png','/images/light-disk-c50-product2.png','/images/light-disk-c50-product1.png'], table1: '/images/cn/light-disk-c50-table1.png', table2: '/images/cn/light-disk-c50-table2.png'},
      {id: 'c60', current: false, bannerList: ['/images/light-disk-c60-product2.png','/images/light-disk-c60-product1.png','/images/light-disk-c60-product3.png'], table1: '/images/cn/light-disk-c60-table1.png', table2: '/images/cn/light-disk-c60-table2.png'}
    ]
  },
  changeLightDiskIndex (state, index) {
    state.lightDiskIndex = index
    for (let i = 0; i < state.lightDiskList.length; i++) {
      if (i === index) {
        state.lightDiskList[i].current = true
      } else {
        state.lightDiskList[i].current = false
      }
    }
  }
}
