import { createRouter, createWebHashHistory } from 'vue-router'

const routes = [
  {
    path: '/',
    name: 'Index',
    component: () => import('../views/Index.vue')
  },{
    path: '/about',
    name: 'About',
    component: () => import('../views/About.vue')
  },{
    path: '/road',
    redirect: '/road/light',
    component: () => import('../views/Wheel.vue'),
    children: [
      {
        path: 'light',
        meta: {
          requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
        },
        component: () => import('comps/RoadLight.vue')
      },
      {
        path: 'lightdisk',
        meta: {
          requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
        },
        component: () => import('comps/RoadLightDisk.vue')
      },
      {
        path: 'pro',
        meta: {
          requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
        },
        component: () => import('comps/RoadPro.vue')
      },
      {
        path: 'prodisk',
        meta: {
          requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
        },
        component: () => import('comps/RoadProDisk.vue')
      }
    ]
  },{
    path: '/mountain',
    redirect: '/mountain/default',
    component: () => import('../views/Wheel.vue'),
    children: [
      {
        path: 'default',
        meta: {
          requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
        },
        component: () => import('comps/MountainDefault.vue')
      }
    ]
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
